import {Component} from '@angular/core';
import {ModalController, NavController} from 'ionic-angular';
import {ApiProvider} from '../../providers/api/api';
import {ProfilePage} from "../profile/profile";
import {RequestPage} from "../request/request";
import {RequestsPage} from "../requests/requests";
import {Camera, CameraOptions} from '@ionic-native/camera';
import {OrdonancePage} from "../ordonance/ordonance";

@Component({
  selector: 'page-home',
  templateUrl: 'home.html',
  providers: [[Camera]]
})
export class HomePage {

  searchQuery: string = '';
  items: any[];
  item: any;
  request: Array<any> = [];
  requestSent: any;
  req: any;
  anwcount: any;
  options: any;

  constructor(public navCtrl: NavController, public api: ApiProvider, public camera: Camera, public modalCtrl: ModalController) {
    this.requestSent = this.api.reqSent;
    if (this.api.curReqkey) {
      this.api.requestRestarter()
    }
    this.api.afdb.object('dispomed_requests/' + this.api.curReqkey)
      .valueChanges()
      .subscribe((data: any) => {
        this.req = data;
      });

    this.api.afdb.list('dispomed_requests/' + this.api.curReqkey+'/answers')
      .snapshotChanges(['child_added'])
      .subscribe((data: any) => {
        if(data.answers){
          console.log('ans')
          this.api.localNotif('Vous avez une nouvelle réponse pour votre requête!');
        }
      });

  }

  initializeItems() {
    this.items = this.api.druglist;
    /*this.items = [
      {'id':'1','name':'Paracetamol'},
      {'id':'2','name':'Viagra'},
    ];*/
  }

  ionViewDidLoad() {

  }

  getItems(ev: any) {
    // Reset items back to all of the items
    this.initializeItems();

    // set val to the value of the searchbar
    let val = ev.target.value;

    // if the value is an empty string don't filter the items
    if (val && val.trim() != '') {
      let x = this.items.filter((item: any) => {
        return (item.name.toLowerCase().indexOf(val.toLowerCase()) > -1);
      });
      this.items = x.slice(0, 9);
    }
    else {
      return this.items = null;
    }
  }

  addMed(item) {

    //@TODO trouver le moyen d'ajouter les éléments (medicaments choisits) au tableau des medicament sans duplication
    /*if (this.request) {
      this.request[item.id]=item;
    }
    else {
      let x =new Array();
      x[item.id]=item
      this.request = x
    }*/
    item.answers = [];
    this.request.push(item);
    //console.log(this.request);
    this.api.presentToast(item.name + ' ajouté')
    window.scrollTo(0,0);
    this.items = null;
  }

  rmMed(id) {
    this.request.splice(id, 1)
  }

  sendRequest() {
    this.anwcount = 0;
    if (!this.api.user.city) {
      this.api.promptVille(this.request)
    }
    else {
      this.api.sendRequest(this.request)
    }
    this.navCtrl.setRoot(HomePage);
  }

  goProfile() {
    this.navCtrl.push(ProfilePage)
  }

  goRequest() {
    this.navCtrl.push(RequestPage, this.req)
  }

  goRequests() {
    if (this.api.user.isofficinevalidated == false) {
      this.api.showAlert('Votre compte pharmacien n\'est pas encore validée.', '');
      return false
    }
    else {
      this.navCtrl.push(RequestsPage)
    }
  }

  takePicture() {
    const options: CameraOptions = {
      quality: 80,
      destinationType: this.camera.DestinationType.DATA_URL,
      //destinationType: 1,
      encodingType: this.camera.EncodingType.JPEG,
      mediaType: this.camera.MediaType.PICTURE,
      correctOrientation: true,
      targetWidth: 1024,
      targetHeight: 1024
    };

    this.camera.getPicture(options).then((imageData) => {
      // imageData is either a base64 encoded string or a file URI
      // If it's base64:
      //let base64Image = 'data:image/jpeg;base64,' + imageData;

      console.log(imageData.length);
      let item = {id: Date.now(), name: 'ORDONANCE', image: imageData};
      this.addMed(item)

    }, (err) => {
      // Handle error
      console.log()
    });

  }

  showPhoto(item) {
    const modal = this.modalCtrl.create(OrdonancePage, item);
    modal.present();
  }
}



