import {Component} from '@angular/core';
import {IonicPage, ModalController, NavController, NavParams} from 'ionic-angular';
import {ApiProvider} from "../../providers/api/api";
import {PharmacyPage} from "../pharmacy/pharmacy";
import {OrdonancePage} from "../ordonance/ordonance";

/**
 * Generated class for the HistoryreqPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-historyreq',
  templateUrl: 'historyreq.html',
})
export class HistoryreqPage {

  request: any;

  constructor(public navCtrl: NavController, public navParams: NavParams, public api: ApiProvider, public modalCtrl: ModalController) {
    this.request = this.navParams.data

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad RequestPage');
  }

  toggleAns(item) {

    if (!item.a) {
      item.a = true;
    }
    else if (item.a) {
      item.a = false;
    }
  }

  goPharma(item) {
    let profileModal = this.modalCtrl.create(PharmacyPage, item);
    profileModal.present();
  }

  showPhoto(item) {
    const modal = this.modalCtrl.create(OrdonancePage, item);
    modal.present();
  }
}



