import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams, ViewController} from 'ionic-angular';
import {ApiProvider} from "../../providers/api/api";

/**
 * Generated class for the PharmacyPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-pharmacy',
  templateUrl: 'pharmacy.html',
})
export class PharmacyPage {

  pharmacy: any = {};

  constructor(public navCtrl: NavController, public navParams: NavParams, public viewCtrl: ViewController, public api: ApiProvider) {
    this.pharmacy = this.navParams.data;
    //console.log(this.pharmacy);
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad PharmacyPage');
  }

  dismiss() {
    this.pharmacy = null;
    this.viewCtrl.dismiss();
  }
}



