import {Component} from '@angular/core';
import {IonicPage, ModalController, NavController, NavParams} from 'ionic-angular';
import {ApiProvider} from "../../providers/api/api";
import {PharmacyPage} from "../pharmacy/pharmacy";
import {OrdonancePage} from "../ordonance/ordonance";

/**
 * Generated class for the RequestPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-request',
  templateUrl: 'request.html',
})
export class RequestPage {

  request: any;

  constructor(public navCtrl: NavController, public navParams: NavParams, public api: ApiProvider, public modalCtrl: ModalController) {


    if (this.navParams.data != null) {
      this.api.afdb.object('dispomed_requests/' + this.navParams.data.key)
        .valueChanges()
        .subscribe(data => {
          this.request = data;
        })
    }
    else {
      this.request = this.api.curafdbReq
    }

    //console.log(this.request);
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad RequestPage');
  }

  toggleAns(item) {

    if (!item.a) {
      item.a = true;
    }
    else if (item.a) {
      item.a = false;
    }
  }

  goPharma(item) {
    let profileModal = this.modalCtrl.create(PharmacyPage, item);
    profileModal.present();
  }

  answer(rkey, did) {
    this.api.promptAnswer(rkey, did)
  }

  showPhoto(item) {
    const modal = this.modalCtrl.create(OrdonancePage, item);
    modal.present();
  }
}



