import {HttpClient} from '@angular/common/http';
import {Injectable} from '@angular/core';
import {AlertController, LoadingController, ToastController} from 'ionic-angular';
import moment from 'moment';
import {AngularFireAction, AngularFireDatabase} from "@angular/fire/database";
import {Observable} from "rxjs";
import {BehaviorSubject} from "rxjs/BehaviorSubject";
import { LocalNotifications } from '@ionic-native/local-notifications';

/*
  Generated class for the ApiProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class ApiProvider {

  items$: Observable<AngularFireAction<firebase.database.DataSnapshot>[]>;
  size$: BehaviorSubject<string | null>;

  user: any;
  uid: any;
  isofficine: any;
  reqSent: boolean = false;
  myrequests: any[];
  alertquests: any[];
  wrequests: any[];
  druglist: any[];
  officines: any[];
  towns: any[];
  countDown: any;
  countDownDate: any;
  countDownExpires: any = false;
  curReq: any;
  tempcity: any;
  curafdbReq: any;
  curReqkey: any;
  gmapUrl: any = 'https://www.google.com/maps/search/?api=1&query=';
  xDate: any = Date;
  moment: any;
  uploadPercent: Observable<number>;
  downloadURL: Observable<string>;

  constructor(private localNotifications: LocalNotifications, public http: HttpClient, public loadingCtrl: LoadingController, public toastCtrl: ToastController, private alertCtrl: AlertController, public afdb: AngularFireDatabase) {
    console.log('Hello ApiProvider Provider');
    if (!localStorage.getItem('dispomed_uid')) {
      this.uid = Date.now();
      this.afdb.object('dispomed_users/' + this.uid).set({name: 'anonymous'});
      localStorage.setItem('dispomed_uid', this.uid.toString());
    }
    else {
      this.uid = localStorage.getItem('dispomed_uid');
    }


    this.afdb.object('dispomed_users/' + this.uid).valueChanges().subscribe(data => {
      //
      if(!data){
        this.user = {name: 'anonymous'}
      }else{
        this.user = data;
      }
    });

    this.curReq = JSON.parse(localStorage.getItem('dispomed_cureq'));
    this.curReqkey = localStorage.getItem('dispomed_curReqkey');
    this.moment = moment;
    this.reqSent = !!localStorage.getItem('dispomed_reqsent');
    this.isofficine = !!localStorage.getItem('dispomed_isofficine');
    this.curafdbReq = this.afdb.object('dispomed_requests/' + this.curReqkey).valueChanges();

    this.wrequests = [];
    this.myrequests = [];
    this.alertquests = [];

    this.afdb.list('dispomed_requests/', ref => ref.orderByChild('clientid').equalTo(this.uid))
      .valueChanges()
      .subscribe((data) => {
      this.myrequests = data;
      });

    this.afdb.list('dispomed_requests/', ref => ref.orderByChild('expire').equalTo(false))
      .valueChanges()
      .subscribe((data: any) => {
        this.alertquests = data.filter(
          r => r.city == this.user.city.id);
      });

    this.http.get('assets/data/drugslist.json')
      .subscribe((data: any) => {
        this.druglist = data;
        //console.dir(this.druglist);
      });

    this.http.get('assets/data/officines.json')
      .subscribe((data: any) => {
        this.officines = data;
        //console.dir(this.druglist);
      })
    this.http.get('assets/data/towns.json')
      .subscribe((data: any) => {
        this.towns = data;
        //console.dir(this.druglist);
      })
  }

  getCurrentRequest() {
    return this.curReq;
  }

  localNotif(message){
    this.localNotifications.schedule({
      id: Math.floor(Math.random() * Math.floor(1000)),
      text: message,
    });
  }

  sendRequest(request) {
    this.curReq = {};
    this.curReq.drugs = request;
    this.curReq.client = this.user.name? this.user.name :  'ananymous' ;
    this.curReq.clientphone = this.user.phone ? this.user.phone : '';
    this.curReq.clientid = this.uid;
    this.curReq.answers = [];
    this.curReq.expire = false;
    this.curReq.city = this.tempcity ? this.tempcity : this.user.city.id;
    this.curReq.date = Date.now();
    let loading = this.loadingCtrl.create({
      content: 'Envoi en cours ...'
    });

    loading.present();
    this.requestTimer()
    //this.curReq = r;
    localStorage.setItem('dispomed_cureq', JSON.stringify(this.curReq));

    //////////////////////////////////////////////////////////////////////////////////////
    let list = this.afdb.list('dispomed_requests');

    list.push(this.curReq).then(e => {
      this.curReqkey = e.key;
      localStorage.setItem('dispomed_curReqkey', e.key);
      //setTimeout(() => {
        loading.dismiss();
        this.curReqkey = e.key;
        localStorage.setItem('dispomed_curReqkey', e.key);
      this.afdb.object('dispomed_requests/' + this.curReqkey).update({'key': this.curReqkey});
        this.curafdbReq = this.afdb.object('dispomed_requests/' + this.curReqkey).valueChanges();

      //this.afdb.object('dispomed_requests/' + this.curReqkey + '/client/' + [this.uid]).set(true)
      //}, 2000);
      //
      this.reqSent = true;
      localStorage.setItem('dispomed_reqsent', 'true')
      //setTimeout(() => {

      //},3000)
    }).then(e => {
      window.location.reload()
    })
  }

  requestTimer() {

    ////////////////////////////////////////////////affaire de date et d'ajout de minutes
    this.xDate.prototype.addMinutes = function (minutes) {
      var copiedDate = new Date(this.getTime());
      return new Date(copiedDate.getTime() + minutes * 60000);
    };

    var add_minutes = function (dt, minutes) {
      return new Date(dt + minutes * 60000);
    }
    //console.log(add_minutes(new Date(2014,10,2), 30).toString());

    //@TODO rajouter les 5min
    this.curReq.countDownDate = add_minutes(this.curReq.date, 5);

    //@TODO rajouter les 60min
    this.curReq.countDownHour = add_minutes(this.curReq.date, 60);
    //console.log(this.curReq.countDownDate)

    let countDownDate = this.curReq.countDownDate;
    let countDownHour = this.curReq.countDownHour;
    let ih = this;
    // Update the count down every 1 second
    let x = setInterval(function () {

      // Get todays date and time
      //let now = new Date().getTime();

      // Find the distance between now and the count down date
      let distance = countDownDate - Date.now();
      // Time calculations for days, hours, minutes and seconds
      //let days = Math.floor(distance / (1000 * 60 * 60 * 24));
      //let hours = Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
      let minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
      let seconds = Math.floor((distance % (1000 * 60)) / 1000);
      //console.log(now, "now", "countDownDate", countDownDate, "distance", distance, "days", days);

      let mm: any = "";
      let ss: any = "";
      if (minutes < 10) {
        mm = "0" + minutes
      } else {
        mm = minutes;
      }
      if (seconds < 10) {
        ss = "0" + seconds
      } else {
        ss = seconds
      }

      // Output the result in an element with id="demo"
      ih.curReq.countDown = /*days + "d " + hours + "h " +*/ mm + ":" + ss + "";

      // If the count down is over, write some text
      if (distance < 0) {
        clearInterval(x);
        ih.curReq.countDown = "EXPIRED";
        ih.curReq.countDownExpires = true;
      }
    }, 1000);
    let y = setInterval(function () {

      // Get todays date and time
      //let now = new Date().getTime();

      // Find the distance between now and the count down date
      let distance = countDownHour - Date.now();
      // Time calculations for days, hours, minutes and seconds
      //let days = Math.floor(distance / (1000 * 60 * 60 * 24));
      //let hours = Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
      let minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
      let seconds = Math.floor((distance % (1000 * 60)) / 1000);
      //console.log(now, "now", "countDownDate", countDownDate, "distance", distance, "days", days);

      let mm: any = "";
      let ss: any = "";
      if (minutes < 10) {
        mm = "0" + minutes
      } else {
        mm = minutes;
      }
      if (seconds < 10) {
        ss = "0" + seconds
      } else {
        ss = seconds
      }

      // Output the result in an element with id="demo"
      ih.curReq.countDownHour = /*days + "d " + hours + "h " +*/ mm + ":" + ss + "";

      // If the count down is over, write some text
      if (distance < 0) {
        clearInterval(y);
        ih.curReq.countDownHour = "EXPIRED";
        ih.curReq.countDownHourExpires = true;
        ih.afdb.object('dispomed_requests/' + ih.curReqkey).update({'expire': true}),
          ih.closeRequest()
      }
    }, 1000);

  }

  requestRestarter() {
    //let req = JSON.parse(localStorage.getItem('dispomed_cureq'));
    this.requestTimer()
  }

  presentToast(msg) {
    let toast = this.toastCtrl.create({
      message: msg,
      duration: 1000
    });
    toast.present();
  }

  promptVille(request) {
    let alert = this.alertCtrl.create({
      title: 'Ville de résidence',
      enableBackdropDismiss: false,
      message: 'Veuillez spécifier votre ville pour une recherche efficace de pharmacies plus proches de vous.',
      buttons: [
        {
          text: 'Annuler',
          handler: data => {

          }
        },
        {
          text: 'Ok',
          handler: data => {
            if (!data) {
              this.promptVille(request);
            } else {
              this.tempcity = data
              this.afdb.object('dispomed_users/' + this.uid).update({city: data});
              //this.sendRequest(request);
            }
          }
        }
      ]
    });

    for (let town of this.towns) {
      alert.addInput({
        type: 'radio',
        label: town.name,
        value: town,
        name: 'city'
      });
    }

    alert.present();
  }

  promptAnswer(rkey, did) {
    let confirm = this.alertCtrl.create({
      title: 'Disponibilité',
      message: 'Vous confirmez avoir ce médicament en stock?',
      buttons: [
        {
          text: 'Non',
          handler: () => {
            this.afdb.object('dispomed_requests/' + rkey + '/drugs/' + did + '/answers/' + [this.uid]).remove();
            this.afdb.object('dispomed_requests/' + rkey + '/answers/' + [this.uid]).remove();
          }
        },
        {
          text: 'Oui',
          handler: () => {

            this.afdb.object('dispomed_requests/' + rkey + '/drugs/' + did + '/answers/' + [this.uid]).set({
              date: Date.now(),
              [this.uid]: true,
              pharmacy: this.user.pharmacy
            });
            this.afdb.object('dispomed_requests/' + rkey + '/answers/' + [this.uid]).set(true)
          }
        }
      ]
    });
    confirm.present();
  }

  presentLoading(msg) {
    let loading = this.loadingCtrl.create({
      content: msg
    });

    loading.present();

    setTimeout(() => {
      loading.dismiss();
    }, 5000);
  }

  editProfile() {
    let prompt = this.alertCtrl.create({
      title: 'Mon profile',
      message: "Enter a name for this new album you're so keen on adding",
      inputs: [
        {
          name: 'name',
          label: 'Nom & Prénom(s)',
          placeholder: 'Nom & Prénom(s)'
        },
        {
          name: 'phone',
          label: 'Mon Téléphone',
          type: 'tel',
          placeholder: 'Mon Téléphone'
        },
        {
          name: 'city',
          label: 'Ma Ville',
          placeholder: 'Ma Ville'
        },
      ],
      buttons: [
        {
          text: 'Annuler',
          handler: data => {
            console.log('Cancel clicked');
          }
        },
        {
          text: 'Enregistrer',
          handler: data => {
            console.log('Saved clicked');

            this.afdb.object('dispomed_users/' + this.uid).update(data)
            //this.user = data;
          }
        }
      ]
    });
    prompt.present();
  }

  saveProfile(user) {
    let loading = this.loadingCtrl.create({
      content: 'Modifications en cours'
    });

    loading.present().then(data => {
      this.afdb.object('dispomed_users/' + this.uid).update(user).then(data => {
        loading.dismiss();
      })
    });

  }

  call(item) {
    window.open('tel://' + item.telephone, '_system')
  }

  locate(item) {
    //@TODO integrer inappbrowser
    window.open(this.gmapUrl + item.latitude + ',' + item.longitude, '_system')
    //console.log(item.geo)
  }

  closeRequest() {
    this.curReq.closed = true;
    this.reqSent = false;
    this.myrequests.push(this.curReq);
    this.afdb.object('dispomed_users/' + this.uid + '/requests/').update({[this.curReqkey]: true})
    this.afdb.object('dispomed_requests/' + this.curReqkey).update({'expire': true});
    localStorage.removeItem('dispomed_curReqkey');
    localStorage.removeItem('dispomed_reqsent');
    localStorage.removeItem('dispomed_cureq');
  }

  registerPharm(user) {
    let loading = this.loadingCtrl.create({
      content: 'Envoi en cours ...'
    });

    loading.present();

    user.cdate = new Date();
    user.isofficine = true;
    user.isofficinevalidated = false;
    console.log(user);

    this.afdb.object('dispomed_users/' + this.uid).update(user).then(data => {
      this.isofficine = true;
      localStorage.setItem('dispomed_isofficine', 'true');

        loading.dismiss();
      //
    });

  }

  countObj(o) {
    let count = 0;
    for (var c in o) {
      count = count + 1;
      c.length;
    }
    return count;
  }

  obj2arr(o) {
    let user = o;

    let arr = [];
    for (let key in user) {
      if (user.hasOwnProperty(key)) {
        arr.push(user[key]);
      }
    }
    return arr;
  }

  dejaRep(p) {
    let ans: any = p.answers ? p.answers : {};
    return ans.hasOwnProperty([this.uid])
  }

  showAlert(sub, msg) {
    const alert = this.alertCtrl.create({
      title: 'DispoMed',
      subTitle: sub,
      message: msg,
      buttons: [{
        text: 'OK',
        handler: data => {
          //console.log('Cancel clicked');
        }
      }]
    });
    alert.present();
  }

}



